import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import jsPDF from 'jspdf';
import { Button } from '@material-ui/core';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
  },
  title: {
    fontSize: 14,
  },
  body: {
    fontSize: '16px',
  },
  action: {
    display: "flex",
    justifyContent: "space-around"
  }
});

export default function UserCard({ elem }) {
  const classes = useStyles();

  const result = `
   

         
   ${elem?.objectType},   : ${
    elem?.address
  }

  ${
    elem?.startDate
  }   " "-"  
       ${
    elem?.objectType
  },  
  : ${elem?.address}.

          
    00394  20.07.2020 ,   "
    " " ( " "").

          
         00392  20.07.2020 ,
    "   "
  " ( " "").

         :
  - ${elem?.wallType}
  - ${elem?.wallDamage}
      : ${elem?.roofDamage}.
      : ${elem?.beamWater}.

      :
  -   () : ${elem?.beam[0]?.result} (${
    elem?.beam[0]?.deflection
  }).
  -   : ${elem?.wall[0]?.conclusion}
  -    : ${
    elem?.concrete[0]?.binding
  }

     ,   
   ,   ,        
     ${
    elem?.beam[0]?.result !== " " &&
    elem?.wall[0]?.conclusion === "  " &&
    elem?.concrete[0]?.binding
      ? ""
      : " "
  }   
  .
  ${
    elem?.beam[0]?.result === " " ||
    elem?.wall[0]?.conclusion !== "  "
      ? "  ,        , \n    : \n -       .  \n     ,  ,  \n        .  \n     6 . \n           . \n -   -     \n  -  "
      : ""
  }

  : ${elem?.executor?.name[0].toUpperCase() +
    elem?.executor?.name.slice(1) + " " +  elem?.executor?.surName[0].toUpperCase() +
      elem?.executor?.surName.slice(1)}

  `;

  const saveReport = async () => {
    let doc = new jsPDF();
    await doc.addFont("PTSans-Regular.ttf", "PTSans", "normal");
    await doc.setFont("PTSans");
    await doc.setFontSize(10);
    await doc.text(result, 15, 15);
    await doc.save("report.pdf");
  };

  return (
    <Card className={classes.root}>
      <CardContent>

        <Typography className={classes.title} color="textSecondary" gutterBottom>
          : {elem.address} 
        </Typography>
        <Typography className={classes.title} color="textSecondary" gutterBottom>
          : {elem.executor?.name ? elem.executor.name[0].toUpperCase() + elem.executor.name.slice(1) : " "}
        </Typography>

      </CardContent>

      <CardActions className={classes.action}>

        <Typography className={classes.body} color="textSecondary" gutterBottom>
          : {elem?.status === 'done' ? "" : ""}
          {elem?.status === 'new' ? "" : ""}
          {elem?.status === 'work' ? " " : ""}
        </Typography>
        {elem.managed && <Button
                  className={classes.btn}
                  onClick={() => saveReport()}
                  variant="outlined"
                  color="primary"
                >
                   PDF
                </Button>}
      </CardActions>
    </Card>
  );
}
