import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import { Container, CssBaseline } from "@material-ui/core";
import { Button } from "@material-ui/core";
import jsPDF from "jspdf";

const useStyles = makeStyles({
  root: {
    fontFamily: "GOST_type_A",
    minWidth: 275,
    display: "flex",
    justifyContent: "center",
    textAlign: "justify",
    fontSize: "18px",
    textIndent: "20px",
  },
  header: {
    margin: "auto",
    display: "flex",
    maxWidth: "max-content",
  },
  report: {
    display: "flex",
    maxWidth: "800px",
    flexDirection: "column",
    alignContent: "center",
    marginTop: "70px",
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)",
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  btn: {
    margin: ".7rem auto",
  },
  btnContainer: {
    display: "flex",
    flexWrap: "wrap",
    margin: "2rem 0"
  }
});

export default function Report({
  setOpenProject,
  setActiveProject,
  activeProject,
}) {
  const classes = useStyles();

  const [findProject, setFindProject] = useState(null);

  const setFunc = () => {
    setActiveProject(null);
    setOpenProject(false);
  };

  useEffect(() => {
    async function getProject() {
      const response = await fetch("/projects");

      const result = await response.json();

      setFindProject(result.filter((el) => el._id === activeProject)[0]);
    }

    getProject();

    return () => {
      setOpenProject(false);
    };
  }, [activeProject, setOpenProject]);

  function getPerson() {
    fetch("/mail", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email: findProject.email,
        data: result,
        id: activeProject
      }),
    });
  }

  const result = `
   

         
   ${findProject?.objectType},   : ${
    findProject?.address
  }

  ${
    findProject?.startDate
  }   " "-"  
       ${
    findProject?.objectType
  },  
  : ${findProject?.address}.

          
    00394  20.07.2020 ,   "
    " " ( " "").

          
         00392  20.07.2020 ,
    "   "
  " ( " "").

         :
  - ${findProject?.wallType}
  - ${findProject?.wallDamage}
      : ${findProject?.roofDamage}.
      : ${findProject?.beamWater}.

      :
  -   () : ${findProject?.beam[0]?.result} (${
    findProject?.beam[0]?.deflection
  }).
  -   : ${findProject?.wall[0]?.conclusion}
  -    : ${
    findProject?.concrete[0]?.binding
  }

     ,   
   ,   ,        
     ${
    findProject?.beam[0]?.result !== " " &&
    findProject?.wall[0]?.conclusion === "  " &&
    findProject?.concrete[0]?.binding
      ? ""
      : " "
  }   
  .
  ${
    findProject?.beam[0]?.result === " " ||
    findProject?.wall[0]?.conclusion !== "  "
      ? "  ,        , \n    : \n -       .  \n     ,  ,  \n        .  \n     6 . \n           . \n -   -     \n  -  "
      : ""
  }

  : ${findProject?.executor?.name[0].toUpperCase() +
    findProject?.executor?.name.slice(1) + " " +  findProject?.executor?.surName[0].toUpperCase() +
      findProject?.executor?.surName.slice(1)}

  `;
  const saveReport = async () => {
    let doc = new jsPDF();
    await doc.addFont("PTSans-Regular.ttf", "PTSans", "normal");
    await doc.setFont("PTSans");
    await doc.setFontSize(10);
    await doc.text(result, 15, 15);
    await doc.save("report.pdf");
  };

  return (
    <>
      {findProject && (
        <Container>
          <CssBaseline />
          <Card className={classes.root}>
            <CardContent className={classes.report}>
              <Typography className={classes.header} variant="h4">
                  
              </Typography>
              <br />
              <Typography variant="inherit">
                       
                 {findProject?.objectType},   :{" "}
                {findProject?.address}. 
              </Typography>
              <br />
              <Typography variant="inherit">
                {findProject?.startDate}   "
                "-"    
                   {findProject?.objectType},
                  : {findProject?.address}.
              </Typography>
              <br />
              <Typography variant="inherit">
                       
                   00394  20.07.2020 ,
                  "  
                " " ( " "").
                <br />
              </Typography>
              <Typography variant="inherit">
                      
                      
                  00392  20.07.2020 ,  
                "   " "
                ( " "").
              </Typography>
              <br />
              <Typography variant="inherit">
                       :
                <br />- {findProject?.wallType}
                <br />- {findProject?.wallDamage}
                <br />
                <br />
                    :{" "}
                {findProject?.roofDamage}.<br />
                    :{" "}
                {findProject?.beamWater}.
              </Typography>
              <br />

              <Typography variant="inherit">
                    :
                <br />
                -   () :{" "}
                {findProject?.beam[0]?.result} (
                {findProject?.beam[0]?.deflection}).
                <br />
                -   :{" "}
                {findProject?.wall[0]?.conclusion}<br />
                -    :{" "}
                {findProject?.concrete[0]?.binding}
              </Typography>
              <br />

              <Typography variant="inherit">
                   , 
                   ,   ,
                       
                 {" "}
                {findProject?.beam[0]?.result !== " " &&
                findProject?.wall[0]?.conclusion === "  " &&
                findProject?.concrete[0]?.binding
                  ? ""
                  : " "}{" "}
                   .
                {findProject?.beam[0]?.result === " " ||
                findProject?.wall[0]?.conclusion !== "  " ||
                findProject?.concrete[0]?.binding ? (
                  <Typography variant="inherit">
                    <br />
                      ,    
                        ,   
                    :
                    <br />
                    -     
                      .    
                    ,  ,   
                         .
                          6
                    .      
                         .
                    <br />
                    -   - 
                        -  .
                  </Typography>
                ) : (
                  ""
                )}
              </Typography>
              <br />

              <Typography variant="h5">
                :{" "}
                {findProject?.executor?.name[0].toUpperCase() +
                  findProject?.executor?.name.slice(1) + " " +  findProject?.executor?.surName[0].toUpperCase() +
                    findProject?.executor?.surName.slice(1)}
                
              </Typography>
              <Container className={classes.btnContainer}>
                <Button
                  className={classes.btn}
                  onClick={() => saveReport()}
                  variant="outlined"
                  color="primary"
                >
                   PDF
                </Button>

                <Button
                  variant="outlined"
                  color="primary"
                  className={classes.btn}
                  onClick={getPerson}
                 
                >
                   
                </Button>

                <Button
                  className={classes.btn}
                  onClick={() => setFunc()}
                  variant="outlined"
                  color="primary"
                >
                    
                </Button>
              </Container>
            </CardContent>
          </Card>
        </Container>
      )}
    </>
  );
}
